@import settings

.mainTitle
	font-size: 48px

.secondTitle
	font-size: 36px	

.projects__list
	display: flex
	justify-content: center

.projects__item
	display: flex
	flex-direction: column	
	margin-right: 80px
	width: 400px
	border: 0.5px solid $black
	border-radius: 10px	
	padding: 20px
	top: 0
	transition: box-shadow 0.4s ease-out, top 0.2s ease-out,

.projects__item:hover
	position: relative
	top: -2px
	box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.25)

.projects__item:last-child
	margin-right: 0

.projects__title
	min-height: 50px

.projects__img
	border-radius: 5px	

.projects__link:hover
	color: $hover	

.projects__link:active
	color: $active		